.video-container {
  position: fixed;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  transition: width 0.5s ease, height 0.5s ease;
}

.video-container.resized {
  width: 85%;
  height: 80%;
  border: 2px solid #ccc;
  box-shadow: 0 2px 4px rgba(0, 10px, 0, 0.2);
}

.video-element {
  width: 100%;
  height: 100%;
}

body {
  background-color: red;
}
